    let currentApp = viewModel.apps.find((app) => app.ID == viewModel.studio.appId);
    viewModel.studio.dnsEndpoint = currentApp?.cloud?.azure?.dnsEndpoint;
    viewModel.studio.webPublishDate = currentApp?.cloud?.azure?.web?.publishDate.toLocaleString();
    viewModel.studio.webPublishedBy = currentApp?.cloud?.azure?.web?.publishedBy.split("@")[0];
    viewModel.studio.mobilePublishDate = currentApp?.cloud?.azure?.mobile?.publishDate.toLocaleString();
