import { ILogItem, LogLevel, LogLevelLookup } from "../logger";
import { ISocket } from "./iSocket";

export enum ReconnectType {
    BackOff,
    Urgent,
}
export enum ConnectReason {
    ConnectCall,
    SendReconnect,
    ErrorReconnect,
    CloseReconnect,
}
interface IReconnectOptions {
    intervalMs: number;
    type: ReconnectType;
}
export interface IDomSocketOptions {
    reConnect?: IReconnectOptions;
    logCallBack?: (logItem: ILogItem) => void;
    onMessage?: (message: any) => void;
    onConnect?: (reason: ConnectReason) => void;
    onSocketStatus?: (status: DomSocketStatus) => void;
    minLogLevel?: LogLevel;
}
export enum DomSocketStatus {
    Initial,
    Disconnected,
    WaitingToConnect,
    Connecting,
    Connected,
}
export class DomSocket implements ISocket {
    //https://developer.mozilla.org/en-US/docs/Web/API/CloseEvent#Status_codes
    private static CodeAndReasons = { userCloded: { code: 4000, reason: "user closed" } };

    private ws?: WebSocket;
    private options: IDomSocketOptions;
    private url: string;
    private id: string;
    private seqErrCount: number;
    private reconnectTryCount: number;
    private timerID?: number;
    private msgSenderList: Array<() => void>;

    private _status: DomSocketStatus;
    public get status(): DomSocketStatus {
        return this._status;
    }
    public set status(value: DomSocketStatus) {
        if (value === this._status) { return; }
        this._status = value;
        this.options.onSocketStatus?.(this._status);
    }
    constructor(url: string, options: IDomSocketOptions = {}) {
        this._status = DomSocketStatus.Initial;
        this.msgSenderList = [];
        this.seqErrCount = 0;
        this.reconnectTryCount = 0;
        this.timerID = undefined;
        this.url = url;
        this.id = url.length > 40 ? url.substring(0, 40) : url;
        this.options = options;
        this.options.minLogLevel = this.options.minLogLevel || LogLevel.log;
    }


    public set OnConnect(cb: (reason: ConnectReason) => void) {
        this.options.onConnect = cb;
    }
    public set OnMessage(cb: (message: any) => void) {
        this.options.onMessage = cb;
    }
    public set ReConnect(option: IReconnectOptions) {
        this.options.reConnect = option;
    }
    public set MinLogLevel(minLogLevel: LogLevel) {
        this.options.minLogLevel = minLogLevel;
    }
    public get Status(): DomSocketStatus {
        return this.status;
    }
    public set onSocketStatus(cb: (status: DomSocketStatus) => void) {
        this.options.onSocketStatus = cb;
    }


    public async Send(message: object | string): Promise<boolean | string> {
        const data: string = message instanceof Object ? JSON.stringify(message) : message;
        return new Promise<boolean | string>((res, rej) => {
            const msgSender = () => {
                this.log({ message: "Sending msg: " + data, level: LogLevel.debug });
                this.ws!.send(data);
                res(true);
            };
            const reason = this.reConnect(ConnectReason.SendReconnect, msgSender);
            if (reason) {
                res(reason);
            }
        });
    }
    /**
     * 
     * @param code 4200-4300. -> https://developer.mozilla.org/en-US/docs/Web/API/CloseEvent#Status_codes
     * @param reason 
     */
    public Close(code?: number, reason?: string) {
        const ws = this.ws;
        this.ws = undefined;
        if (!ws) { return; }
        const userCloded = DomSocket.CodeAndReasons.userCloded;
        code = code ?? userCloded.code;
        reason = reason ?? userCloded.reason;
        this.log({ message: `Close call code: ${code} reason: ${reason}`, level: LogLevel.debug });
        this.options.reConnect = undefined;
        ws.close(code, reason);
    }
    public Connect() {
        this.reConnect(ConnectReason.ConnectCall);
        return this;
    }


    private log(options: { message: string, level: LogLevel, additional?: any }) {
        if (this.options.minLogLevel && LogLevelLookup[options.level] < LogLevelLookup[this.options.minLogLevel]) { return; }
        const message = `Client ${this.id}: ${options.message}`;
        this.options.logCallBack?.({ message, additional: options.additional, level: options.level, timeFormat: "time" });
    }

    private reConnect(reason: ConnectReason, msgSender?: () => void) {
        switch (this.ws?.readyState) {
            case WebSocket.OPEN:
                msgSender?.(); return; // if open, send and return
            case WebSocket.CONNECTING:
                msgSender && this.msgSenderList.push(msgSender); return; // if connecting, queue and return
            case WebSocket.CLOSED:
            case WebSocket.CLOSING:
            default: // ws -> null
                break;
        }
        if (reason == ConnectReason.ConnectCall) {
            msgSender && this.msgSenderList.push(msgSender); // queue and try connection
            this.connect(reason);
            return;
        }
        if (!this.options.reConnect) { return "Closed"; } // no reconnect, fail and return

        msgSender && this.msgSenderList.push(msgSender); // queue and try re-connection
        if (this.timerID) { return; } // it is allready timered

        this.status = DomSocketStatus.WaitingToConnect;
        this.reconnectTryCount++;
        const timeoutCounter = this.calculateReconnectInterval(this.options.reConnect);
        this.timerID = window.setTimeout(() => {
            this.timerID = undefined;
            this.connect(reason);
        }, timeoutCounter);
    }
    private connect(reason: ConnectReason) {
        this.status = DomSocketStatus.Connecting;
        this.ws = new WebSocket(this.url);
        this.ws.onclose = this.onSocketClose.bind(this);
        this.ws.onopen = (openEvent: Event) => this.onSocketOpen(openEvent, reason);
        this.ws.onerror = this.onSocketError.bind(this);
        this.ws.onmessage = this.onSocketMessage.bind(this);
    }

    private onSocketError(errorEvent: Event) {
        let reconMsg = "";
        if ((!this.ws || this.ws.readyState == WebSocket.CLOSED) && this.options.reConnect) {
            this.seqErrCount++;
            reconMsg = this.options.reConnect ? ", errCount: " + this.seqErrCount : "";
            this.reConnect(ConnectReason.ErrorReconnect);
        }
        this.log({ message: "error" + reconMsg, level: LogLevel.error, additional: errorEvent });
    }

    private calculateReconnectInterval(options: IReconnectOptions) {
        let calculatedInterval = options.intervalMs;

        if (options.type == ReconnectType.BackOff) {
            const fibonacciArray = [1, 2, 3, 5, 8, 13, 21, 34, 55];
            let intervalUnit = options.intervalMs / 5
            let fibonacciRatio = this.reconnectTryCount > fibonacciArray.length ? fibonacciArray[fibonacciArray.length - 1] : fibonacciArray[this.reconnectTryCount - 1]
            calculatedInterval = Math.round(intervalUnit * fibonacciRatio);
        }
        return calculatedInterval;
    }



    private onSocketOpen(openEvent: Event, reason: ConnectReason) {
        this.status = DomSocketStatus.Connected;
        this.log({ message: "connected", level: LogLevel.log });
        this.options.onConnect?.(reason);
        this.seqErrCount = 0;
        this.reconnectTryCount = 0;
        while (this.msgSenderList.length) {
            this.msgSenderList.pop()?.();
        }
    }
    private onSocketClose(closeEvent: CloseEvent) {
        this.status = DomSocketStatus.Disconnected;
        this.log({ message: "error", level: LogLevel.log });
        const userCloded = DomSocket.CodeAndReasons.userCloded;
        if (closeEvent.code == userCloded.code && closeEvent.reason == userCloded.reason) {
            return;
        }
        if (!this.options.reConnect) {
            return;
        }
        this.reConnect(ConnectReason.CloseReconnect);
    }
    private onSocketMessage(messageEvent: MessageEvent) {
        this.log({ message: "message", level: LogLevel.debug });
        this.options.onMessage?.(messageEvent.data);
    }



}